<template>
  <div class="chart-container">
    <el-row>
      <el-col :span="24">
        <div id="chartBar" class="chart-style"></div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12">
        <div id="chartPie" class="chart-style"></div>
      </el-col>
      <el-col :span="12">
        <div id="chartLine" class="chart-style"></div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import echarts from "echarts";
export default {
  name: "Dashboard",
  data() {
    return {
      chartBar: null,
      chartPie: null
    };
  },

  methods: {
    drawBarChart() {
      var option = {
        title: { text: "" },
        tooltip: {},
        xAxis: {
          axisLabel: {
            interval: 0
          },
          data: [
            "",
            "",
            "",
            "",
            "",
            "CR-V",
            "",
            "GLC",
            "RAV4",
            "",
            "911",
            "GT-R",
            "",
            "",
            ""
          ]
        },
        yAxis: {},
        series: [
          {
            name: "",
            type: "bar",
            data: [13, 16, 24, 21, 8, 13, 3, 6, 9, 9, 2, 4, 5, 18, 3]
          }
        ]
      };
      this.chartColumn = echarts.init(document.getElementById("chartBar"));
      this.chartColumn.setOption(option);
    },
    drawPieChart() {
      var option = {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "left",
          data: [
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            ""
          ]
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            data: [
              { value: 1, name: "" },
              { value: 5, name: "" },
              { value: 3, name: "" },
              { value: 1, name: "" },
              { value: 1, name: "" },
              { value: 3, name: "" },
              { value: 1, name: "" },
              { value: 2, name: "" }
            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      this.chartPie = echarts.init(document.getElementById("chartPie"));
      this.chartPie.setOption(option);
    },
    drawLineChart() {
      var option = {
        title: {
          text: ""
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["", "SUV", "MPV", ""]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: ["2012", "2013", "2014", "2015", "2016", "2017", "2018"]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "MPV",
            type: "line",
            stack: "",
            data: [0.1, 0.1, 0.11, 0.11, 0.13, 0.15, 0.2]
          },
          {
            name: "SUV",
            type: "line",
            stack: "",
            data: [0.1, 0.09, 0.12, 0.13, 0.15, 0.19, 0.24]
          },
          {
            name: "",
            type: "line",
            stack: "",
            data: [0.3, 0.32, 0.35, 0.36, 0.4, 0.44, 0.46]
          }
        ]
      };
      this.chartLine = echarts.init(document.getElementById("chartLine"));
      this.chartLine.setOption(option);
    },
    drawCharts() {
      this.drawBarChart();
      this.drawPieChart();
      this.drawLineChart();
    }
  },
  mounted: function() {
    this.drawCharts();
  }
};
</script>
<style scoped>
.chart-container {
  height: 600px;
  overflow: auto;
}
.el-col {
  padding: 30px 20px;
}
.chart-style {
  width: 100%;
  height: 400px;
}
</style>



